import { DeleteOutlined } from '@ant-design/icons'
import { Box, Theme } from '@material-ui/core'
import { makeStyles } from '@material-ui/styles'
import { Avatar, Button, Popconfirm, Table, Tooltip } from 'antd'
import Column from 'antd/lib/table/Column'
import { UserTooltip } from 'components/Common'
import { Owner } from 'models'

interface Props {
    dataTable: any[]
    loading: boolean
    handleRemoveFeedback: (feedbackId: string) => void
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        padding: theme.spacing(1),

        '& .owner-info': {
            display: 'grid',
            placeItems: 'center'
        }
    }
}))

const FeedbackTable = ({ dataTable, loading, handleRemoveFeedback }: Props) => {
    const classes = useStyles()

    return (
        <Box className={classes.root}>
            <Table dataSource={dataTable} loading={loading}>
                <Column width={30} title="STT" dataIndex="index" key="index" />

                <Column
                    title="Ngi gp "
                    dataIndex="owner"
                    key="owner"
                    width={130}
                    align='center'
                    render={(data: Owner) => (
                        <UserTooltip data={data}>
                            <Avatar
                                src={data.avatarUrl}
                                size='large'
                            />
                        </UserTooltip>
                    )}
                />

                <Column title="Ch " width={180} dataIndex="category" key="category" />

                <Column title="Ni dung" dataIndex="content" key="content" />

                <Column title="Thi gian" dataIndex="date" key="date" />

                <Column
                    title="Hnh ng"
                    key="action"
                    align='center'
                    render={(text, record: any) => (
                        <Popconfirm
                            title='Bn c mun xa?'
                            okText='Xa'
                            cancelText='Hy'
                            okButtonProps={{ danger: true }}
                            onConfirm={() => handleRemoveFeedback(record.key)}
                        >

                            <Tooltip title='Xa'>
                                <Button type='primary' danger icon={<DeleteOutlined />} />
                            </Tooltip>
                        </Popconfirm>
                    )}
                />
            </Table>
        </Box>
    )
}

export default FeedbackTable
